form {
    width: 90%;
    max-width: 350px;
    background-color: #fff;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 5px;
}

label {
    text-align: center;
    font-weight: bold;
    font-size: 25px;
    color: #1E1E1F;
    margin-bottom: 26px;
}

textArea {
    background-color: #F9FBFC;
    border: 2px solid #202329;
    border-radius: 2px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 30px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
    border-radius: 5px;
}

textarea::placeholder {
    color: #A5A5A5;
    font-family: 'Monserrat';
    font-weight: 400;

}

textarea:focus {
    outline-color: #61DAFA;
}

.TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
}

.TodoForm-button{
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: sans-serif;
}

.TodoForm-button--add{
    background: #61DAFA;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
    font-size: 18px;
    padding: 8px 12px;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    
}
.TodoForm-button--cancel{
    background: transparent;
    font-size: 18px;
    
}