.TodoSearch {
    background: #F9FBFE;
    border-radius: 2px;
    border: 2px solid #202329;
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    text-align: center;
    font-family: sans-serif;
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 
    0.25);
    font-size: 24px;
}

.TodoSearch::placeholder {
    color: #A5A5A5;
    font-family: sans-serif;
    font-weight: 400;
    font-size: 20px;
}

.TodoSearch:focus {
    outline-color: #61DAFA;
}